    color: white;
    width: 20ch;
    height: 4ch;
    font-family: 'Courier New';
    font-size: .65em;
    text-align: center;
    text-decoration: none;
    display: inline-table;
    margin: 20px .25in 0 .25in;
    cursor: pointer;
}